<template>
    <div>
        <header>
            <h2>The evolution of website buliding tools</h2>
        </header>
        <div id="container">
            <!-- swiper -->
            <swiper :options="swiperOption" ref="mySwiper" @slideChangeTransitionEnd="alertclick">
                <swiper-slide v-for="(item, index) in list">
                    <keep-alive>
                        <!--keep-aliveDOM-->
                        <component :is="item.component" :ti='ti' :pi='tindex'></component>
                    </keep-alive>
                </swiper-slide>

                <div class="swiper-pagination" slot="pagination"></div>
            </swiper>

        </div>


    </div>
</template>

<script>
    //import MainScene from './mainscene'
    import PageOne from '../components/year/c1991'
    import PageTwo from '../components/year/c1995'
    import PageThree from '../components/year/c2004'
    import PageFour from '../components/year/c2007'
    import PageFive from '../components/year/c2012'
    import PageSix from '../components/year/c2018'
    export default {
        data() {
            return {
                list: [{
                        component: PageOne
                    },
                    {
                        component: PageTwo
                    },
                    {
                        component: PageThree
                    },
                    {
                        component: PageFour
                    },
                    {
                        component: PageFive
                    },
                    {
                        component: PageSix
                    },

                ],
                swiperOption: {
                    notNextTick: true, //notNextTicknotNextTicktrueNextTickswiperswiperswipertrue
                    direction: 'vertical', //
                    grabCursor: true, //Swiper
                    setWrapperSize: true, //Swiperflexbox(display: flex)Wrapperslidesflexbox
                    autoHeight: true, //truewrappercontainerslide
                    slidesPerView: 1, //sliderslides(carousel)loop 'auto'slidesloop'auto'loopedSlides
                    mousewheel: true, //Swiperfalse
                    mousewheelControl: false, //
                    height: 600,
                    //height: window.innerHeight, // 
                    resistanceRatio: 0, //slide0
                    observeParents: true, //observeSwiperSwiperwindow.resizeSwiper
                    initialSlide: 0,

                    pagination: {
                        el: '.swiper-pagination',
                        clickable: true
                    }
                },
                ti: {
                    astart: 0,
                    aend: 0,
                },

                nowIndex: 0,
                tindex: 0,
                vals: [1, 23500, 51611646, 121892559, 697089489, 1800000000],
            }
        },
        computed: {
            swiper() {
                return this.$refs.mySwiper.swiper
            }
        },
        methods: {
            alertclick() {

                this.tindex = this.swiper.activeIndex

                this.ti.astart = this.vals[this.tindex - 1]
                this.ti.aend = this.vals[this.tindex]

                //
            },
        }


    }

</script>

<style scoped>
    body,
    h1,
    h2,
    p {
        margin: 0;
        padding: 0;
    }

    #container {
        margin: 0 auto;
        width: 600px;
        height: 400px;
    }

</style>
